import React, { Component } from 'react';
import ServiceCard from './ServiceCard';
import './Info.css';

class Info extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 0
    };
  }

  componentDidMount() {
    var tabs = document.querySelectorAll('.image-tab');
    tabs.forEach(function(tab, index) {
      tab.addEventListener('click', function() {
        tabs.forEach(function(tab) {
          tab.classList.remove('selected');
        });
        tab.classList.add('selected');
        this.setState({
          selectedTab: index
        });
      }.bind(this));
    }.bind(this));
  }

  getContent(selectedTab) {
    if (selectedTab === 0) {
      return <div id='personalized-consultation-content' className='content'>
              <p>Consulta de nutrición personalizada online y presencial</p>
              <div className='card-col'>
                <ServiceCard title='Educación nutricional' />
                <ServiceCard title='Sobrepeso y obesidad' />
                <ServiceCard title='Nutrición clínica para patologías'
                             text='Especialidad en patología renal' />
                <ServiceCard title='Nutrición en etapas de la vida'
                             text='Especialidad en tercera edad' />
                <ServiceCard title='Alimentación vegetariana/vegana' />
              </div>
            </div>;
    }

    if (selectedTab === 1) {
      return <div id='workshops-content' className='content'>
              <p>Formaciones y talleres</p>
              <div className='card-col'>
                <ServiceCard title='Formaciones' />
                <ServiceCard title='Charlas' />
                <ServiceCard title='Talleres' />
                <ServiceCard title='Conferencias' />
                <ServiceCard text='Temáticas, contenido, metodología y duración según objetivos y necesidades de la audencia' />
              </div>
            </div>;
    }

    if (selectedTab === 2) {
      return <div id='colaborations-content' className='content'>
              <p>Colaboración con asociaciones, centros y empresas</p>
              <div className='card-col'>
                <ServiceCard size='big' title='Valoración, supervisión y elaboración de menús' text='Para centros, comedores, cafeterías…' />
                <ServiceCard title='Formaciones o sesiones periódicas' text='Para trabajadores y/o usuarios' />
                <ServiceCard title='Asesoramiento transversal' text='En programas de salud y mejora de hábitos' />
              </div>
            </div>;
    }
  }

  render() {
    return (
      <div className='Info'>
        <div className='services-row'>
          <div id='personalized-consultation' className='image-tab selected'></div>
          <div id='workshops' className='image-tab'></div>
          <div id='colaborations' className='image-tab'></div> 
        </div>
        <div className='service-description'>
          { this.getContent(this.state.selectedTab) }
        </div>
      </div>
    );
  }
}

export default Info;
